.page__body {
  margin: 0 auto;
  position: relative;
}

.page__body:has(.page__menu:target) {
  overflow: hidden;
}

.page__menu {
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0);
}

.page__overlay {
  z-index: 2;
  opacity: 1;
  pointer-events: none;
  visibility: hidden;
  background-color: #00000080;
  width: 100vw;
  height: 100vh;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
}

.page:has(.page__menu:target) .page__overlay {
  opacity: 1;
  pointer-events: all;
  visibility: visible;
}

